import React from 'react'
import store from 'store'
import moment from 'moment'
import {
  update,
  mergeAll,
  map,
  last as Rlast,
  filter,
  omit,
  keys,
  sort,
  inc,
  pathOr,
  length,
} from 'ramda'
import {
  Mutation,
  MutationOptions,
  SnippetProps,
  ReportTemplate,
  ReportTemplateSetting,
} from '@/constants/types'
import {
  useReportTemplate as useReportTemplateItem,
  useReportTemplateState,
  useCurrentCategoryOfSnippet,
} from '@/data/useReportTemplate'
import { uuid, getNumberFromString, downloadAsJson } from '@/utils/webHelper'
import { confirm } from '@/utils/message'
const { compose } = require('ramda')

export function useUpdateSnippet<T = ReportTemplateSetting>(options: {
  item: SnippetProps
  onUpdate: (data: any) => void
  defaultValue?: any
}) {
  const { item, onUpdate, defaultValue = {} } = options

  const [setting, setSetting] = React.useState<T>(
    pathOr(defaultValue, ['setting'], item) as T
  )

  const handleOk = (values: T) => {
    setSetting(values)
    onUpdate({ ...item, setting: values })
  }

  return { setting, handleOk }
}

export const useReportTemplates = () => {
  const [reportTemplates] = useReportTemplateState()

  return {
    refetch: () => ({}),
    data: {
      content: reportTemplates,
      total: length(reportTemplates),
      totalPages: Math.floor(length(reportTemplates) / 10) + 1,
    },
    loading: false,
  }
}

export const useAllReportTemplates = () => {
  return useReportTemplateState()
}

export const useReportTemplate = (options: { id?: React.Key; data?: any }) => {
  const { setSnippetList, snippetList, ...rest } = useReportTemplateItem(
    options
  )

  const [category, setCategory] = useCurrentCategoryOfSnippet()

  const handleSnippetUpdate = (data: any[]) => {
    setSnippetList(prev => ({ ...prev, [category]: data }))
  }

  const handleTabChange = (activeKey: string) => {
    setCategory(activeKey)
  }

  const handleTabMove = (order: string[]) => {
    const snippetKeys = keys(snippetList)

    setSnippetList(
      compose(
        mergeAll,
        map((key: string) => ({ [key]: snippetList[key] })),
        sort((a: string, b: string) => {
          const orderA = order.indexOf(a)
          const orderB = order.indexOf(b)

          if (orderA !== -1 && orderB !== -1) {
            return orderA - orderB
          }
          if (orderA !== -1) {
            return -1
          }
          if (orderB !== -1) {
            return 1
          }

          const ia = snippetKeys.indexOf(a)
          const ib = snippetKeys.indexOf(b)

          return ia - ib
        }),
        keys
      )(snippetList)
    )
  }

  const handleTabAdd = () => {
    const last = compose(
      inc,
      pathOr(0, []),
      getNumberFromString,
      Rlast,
      sort((a: any, b: any) => a - b),
      keys
    )(snippetList)

    setSnippetList(prev => ({ ...prev, [`新標籤頁${last}`]: [] }))
    setCategory(`新標籤頁${last}`)
  }

  const handleTabRemove = (targetKey: string) => {
    confirm({
      title: '確認要刪除此頁籤嗎？',
      content: '刪除會連同模版資料一起刪除，確認繼續？',
      onOk: () => {
        setSnippetList(prev => omit([targetKey], prev))

        if (category === targetKey) {
          setCategory(
            compose(
              Rlast,
              filter((key: string) => key !== targetKey),
              keys
            )(snippetList)
          )
        }
      },
    })
  }

  const handleTabNameChange = (targetKey: string) => (
    evt: React.FocusEvent<HTMLSpanElement>
  ) => {
    const newTabName = evt.target.innerHTML

    setSnippetList(prev =>
      compose(
        mergeAll,
        map((key: string) => ({
          [key === targetKey ? newTabName : key]: prev[key],
        })),
        keys
      )(prev)
    )
    setCategory(newTabName)
  }

  const handleTabEdit = (
    e:
      | string
      | React.MouseEvent<Element, MouseEvent>
      | React.KeyboardEvent<Element>,
    action: 'add' | 'remove'
  ) => {
    const handler = action === 'add' ? handleTabAdd : handleTabRemove

    handler(e as string)
  }

  return {
    ...rest,
    category,
    snippetList,
    setCategory,
    setSnippetList,
    handleTabEdit,
    handleTabChange,
    handleTabMove,
    handleTabAdd,
    handleTabRemove,
    handleTabNameChange,
    handleSnippetUpdate,
  }
}

export const useAddNewTemplate = ({ onCompleted }: MutationOptions = {}) => {
  const [reportTemplates, setReportTemplates] = useReportTemplateState()

  const handler = (values: ReportTemplate) => {
    let id = uuid()
    let newList = [
      ...reportTemplates,
      {
        ...values,
        id,
        createdAt: moment(),
        createdAtStr: moment().format('YYYY/MM/DD HH:mm'),
        updatedAt: moment(),
        updatedAtStr: moment().format('YYYY/MM/DD HH:mm'),
      },
    ]

    setReportTemplates(newList)

    store.set('reportTemplateListState', newList)

    if (onCompleted) {
      onCompleted({ ...values, id })
    }
  }

  return [false, handler] as Mutation
}

export const useUpdateTemplate = ({ onCompleted }: MutationOptions = {}) => {
  const [reportTemplates, setReportTemplates] = useReportTemplateState()

  const handler = (values: ReportTemplate) => {
    let index = reportTemplates.findIndex(x => `${x.id}` === `${values.id}`)
    let newList = update(
      index,
      {
        ...values,
        updatedAt: moment(),
        updatedAtStr: moment().format('YYYY/MM/DD HH:mm'),
      },
      reportTemplates
    )

    setReportTemplates(newList)
    store.set('reportTemplateListState', newList)

    if (onCompleted) {
      onCompleted()
    }
  }

  return [false, handler] as Mutation
}

export const useExportTemplate = ({
  data,
  filename,
}: {
  data: ReportTemplate
  filename: string
}) => {
  const handleExport = () => {
    downloadAsJson({
      filename: `${filename}_${data?.name || ''}_${moment().format(
        'YYYYMMDDHHmmss'
      )}`,
      json: data,
    })
  }

  return handleExport
}
