import {
  compose,
  ifElse,
  invoker,
  path,
  identity,
  includes,
  always,
  T,
  join,
  map,
  filter,
  keys,
  equals,
  __,
} from 'ramda'
import { isNil } from '@/utils/webHelper'
import { MutationOptions } from '@/constants/types'
import envStore from '@/constants/env'
const { cond } = require('ramda')

type RequestInit_ = {
  body?: any
  credentials?: RequestCredentials_
  headers?: HeadersInit_
  integrity?: string
  keepalive?: boolean
  method?: string
  mode?: RequestMode_
  referrer?: string
  window?: any
  errorMsg?: boolean
  successMsg?: boolean
  onCompleted?: (data: any) => any
  onError?: (err: Error) => any
}

const contentTypeIs = (s: string) => {
  return compose(
    ifElse(isNil, identity, (c = '') => c.includes(s)),
    invoker(1, 'get')('content-type'),
    path(['headers'])
  )
}

function useLoading() {
  const [loading, setIsLoading] = React.useState(false)

  const withMeta = React.useCallback((handler: any) => {
    setIsLoading(true)

    return handler().then((response: any) => {
      setIsLoading(false)

      return response
    })
  }, [])

  return [loading, withMeta] as [boolean, (handler: any) => any]
}

export function toQueryString(
  paramsObject: {
    [key: string]: any
  } = {}
) {
  if (!paramsObject) {
    return ''
  }

  return compose(
    join('&'),
    map((key: string) =>
      Array.isArray(paramsObject[key])
        ? // convert to key=val1,val2,val3 string
          `${key}=${paramsObject[key]
            .map((val: string | number) => `${encodeURIComponent(val)}`)
            .join(',')}`
        : // convert to key=val string
          `${key}=${encodeURIComponent(paramsObject[key])}`
    ),
    filter(
      (key: string) =>
        paramsObject[key] !== '' &&
        paramsObject[key] !== null &&
        typeof paramsObject[key] !== 'undefined'
    ),
    keys
  )(paramsObject)
}

export const postRequest = (url: string, options: RequestInit_) =>
  fetch(url, { ...options, method: 'POST' })

export const putRequest = (url: string, options: RequestInit_) =>
  fetch(url, { ...options, method: 'PUT' })

export const deleteRequest = (url: string, options: RequestInit_) =>
  fetch(url, { ...options, method: 'DELETE' })

export function request(
  url: string,
  {
    body,
    headers,
    errorMsg = true,
    onCompleted,
    onError,
    ...options
  }: RequestInit_ = {}
) {
  let fullUrl = cond([
    [includes('http'), always(url)],
    [T, always(envStore.apiBaseUrl + url)],
  ])(url)

  return fetch(fullUrl, {
    ...options,
    body: body instanceof FormData ? body : JSON.stringify(body),
    headers: {
      'Content-Type': 'application/json',
      Accept: 'application/json',
      //   authorization: '',
      ...headers,
    },
  })
    .then(async (response: Response) => {
      if (onCompleted) {
        onCompleted(response)
      }

      // if (successMsg) {
      //   success()
      // }

      if (contentTypeIs('application/json')(response)) {
        const json = await response.json()

        if (!response.ok) {
          return Promise.reject(json)
        }

        return json
      }

      if (contentTypeIs('sheet')(response)) {
        return await response.blob()
      }

      return response.text()
    })
    .catch((err: Error) => {
      //   if (errorMsg) {
      //     error(err)
      //   }

      if (onError) {
        onError(err)
      }

      return err
    })
}

export function useRequest<T = any>({
  onCompleted,
  onError,
  ..._options
}: MutationOptions = {}) {
  const [loading, withMeta] = useLoading()

  const [data, setData] = React.useState<any>()
  const [error, setError] = React.useState<Error>()

  const fetcher = (url: string, { body, method, ...options }: RequestInit) => {
    let fullUrl = cond([
      [equals(__, 'GET'), always(`${url}?${toQueryString(body as any)}`)],
      [T, always(url)],
    ])(method)

    return withMeta(async () =>
      request(fullUrl, {
        ..._options,
        ...options,
        method,
        onError: error => {
          if (onError) {
            onError(error)
          }
          setError(error)
        },
        onCompleted: data => {
          if (onCompleted) {
            onCompleted(error)
          }
          setData(data)
        },
      })
    )
  }

  return [
    fetcher,
    {
      loading,
      data,
      error,
    },
  ] as [
    (url: string, options?: RequestInit) => Promise<T>,
    { loading: boolean; data?: any; error?: Error }
  ]
}

export default request
