import React from 'react'
import store from 'store'
import moment from 'moment'
import { update, length } from 'ramda'
import { Mutation, MutationOptions, DataSource } from '@/constants/types'
import { useReportList } from '@/data/useReport'
import { uuid } from '@/utils/webHelper'

export const useReports = () => {
  const [data, setData] = React.useState<DataSource>({
    content: [],
    total: 0,
    totalPages: 0,
  })

  const [reportList, setReportList] = useReportList()

  const refetch = (values?: any) => {
    setData({
      content: reportList,
      total: length(reportList),
      totalPages: Math.floor(length(reportList) / 10) + 1,
    })
  }

  return {
    data,
    refetch,
    loading: false,
    reportList,
    setReportList,
  }
}

export function useReportItem(id: string) {
  const [reportList] = useReportList()

  const [data, setData] = React.useState<any>()

  React.useEffect(() => {
    setData(reportList.find((x: any) => x.id === id))
  }, [id]) // eslint-disable-line

  return data
}

export const useAddNewReport = ({ onCompleted }: MutationOptions = {}) => {
  const [reportList, setReportList] = useReportList()
  const handler = (values: any) => {
    let id = uuid()
    let newList = [
      ...reportList,
      {
        ...values,
        id,
        createdAt: moment(),
        createdAtStr: moment().format('YYYY/MM/DD HH:mm'),
        updatedAt: moment(),
        updatedAtStr: moment().format('YYYY/MM/DD HH:mm'),
      },
    ]

    setReportList(newList)
    store.set('reportListState', newList)

    if (onCompleted) {
      onCompleted({ ...values, id })
    }
  }
  return [false, handler] as Mutation
}

export const useUpdateReport = ({ onCompleted }: MutationOptions = {}) => {
  const [reportList, setReportList] = useReportList()

  const handler = (values: any) => {
    let index = reportList.findIndex((x: any) => `${x.id}` === `${values.id}`)
    let newList = update(
      index,
      {
        ...values,
        updatedAt: moment(),
        updatedAtStr: moment().format('YYYY/MM/DD HH:mm'),
      },
      reportList
    )
    setReportList(newList)
    store.set('reportListState', newList)

    if (onCompleted) {
      onCompleted()
    }
  }
  return [false, handler] as Mutation
}
