import React from 'react'
import styled from 'styled-components'
import { length, keys, pathOr, gt, __, map } from 'ramda'
import cx from 'classnames'
import { Row, Col, Divider, Form } from 'antd'
import { useBreadcrumb } from '@/hooks/useApp'
import { createFormItemLayout } from '@/utils/form'
import { Snippets } from '@/constants/types'
import { Input } from '@/components/input'
import { renderSnippets } from '@/components/snippet-utils'
import { MedicalOrder } from '@/services/pathology'
import theme from '../../theme'
const { compose } = require('ramda')

type Props = {
  className?: string
  style?: React.CSSProperties
  data: {
    template: any
    snippetList: Snippets
    medical: MedicalOrder
  }
}

const formItemLayout = createFormItemLayout(8)
const shortFormItemLayout = createFormItemLayout(3)

const SnippetPreviewWrapper = styled.div`
  .ant-card-head {
    min-height: auto;
  }

  .ant-card-head-title {
    padding-top: 0;
    font-size: 14px;
  }

  .ant-card-body {
    padding-top: 6px;
    padding-bottom: 8px;
    padding-left: 18px;
  }
`

export default function ReportTypingPreview({ className, style, data }: Props) {
  const { snippetList, medical } = data

  useBreadcrumb([{ name: '輸入報告' }, { name: '文件報告' }])

  return (
    <div style={style} className={cx('js-export-pdf', className)}>
      <Row className="mb-3">
        <Col span={24}>
          <div className="text-xl text-center mt-2">
            高雄榮總 <span className="mx-6">病理檢驗部</span> 檢查報告單
          </div>
        </Col>
      </Row>
      <Divider />
      <Row gutter={16}>
        <Col span={8}>
          <Form.Item {...formItemLayout} label="病房床號">
            <Input readOnly bordered={false} value={medical.HBED} />
          </Form.Item>
        </Col>
        <Col span={8}>
          <Form.Item {...formItemLayout} label="病患姓名">
            <Input readOnly bordered={false} value={medical.HNAMEC} />
          </Form.Item>
        </Col>
        <Col span={8}>
          <Form.Item {...formItemLayout} label="病歷號">
            <Input readOnly bordered={false} value={medical.HHISNUM} />
          </Form.Item>
        </Col>
      </Row>
      <Row gutter={16}>
        <Col span={8}>
          <Form.Item {...formItemLayout} label="病患性別">
            <Input readOnly bordered={false} value={medical.HSEX} />
          </Form.Item>
        </Col>
        <Col span={8}>
          <Form.Item {...formItemLayout} label="病患身分">
            <Input readOnly bordered={false} value={'xxxxx'} />
          </Form.Item>
        </Col>
        <Col span={8}>
          <Form.Item {...formItemLayout} label="出生日期">
            <Input readOnly bordered={false} value={medical.HBIRTHDT} />
          </Form.Item>
        </Col>
      </Row>
      <Row gutter={16}>
        <Col span={8}>
          <Form.Item {...formItemLayout} label="申請序號">
            <Input readOnly bordered={false} value={medical.ORREQNO} />
          </Form.Item>
        </Col>
        <Col offset={8} span={8}>
          <Form.Item {...formItemLayout} label="病理號">
            <Input readOnly bordered={false} value={medical.ORWORKNO} />
          </Form.Item>
        </Col>
      </Row>
      <Row>
        <Col span={24}>
          <Form.Item {...shortFormItemLayout} label="醫囑項目">
            <div
              dangerouslySetInnerHTML={{ __html: medical.ORWORD }}
              style={{
                maxWidth: '100%',
                whiteSpace: 'initial',
                borderBottom: '1px solid #d9d9d9',
                paddingBottom: 5,
                paddingLeft: 5,
              }}
            />
          </Form.Item>
        </Col>
      </Row>
      <Row>
        <Col span={24}>
          <Form.Item {...shortFormItemLayout} label="檢查日期">
            <Input readOnly bordered={false} value={'xxxx'} />
          </Form.Item>
        </Col>
      </Row>
      <Row>
        <Col span={24}>
          <Form.Item {...shortFormItemLayout} label="開立科別">
            <Input readOnly bordered={false} value={medical.CSECT} />
          </Form.Item>
        </Col>
      </Row>
      <Divider />
      <Row>
        <Col span={24}>
          <SnippetPreviewWrapper>
            {compose(
              map((key: string) => (
                <div key={key}>
                  {compose(
                    gt(__, 0),
                    length,
                    pathOr([], [key])
                  )(snippetList) && (
                    <h2
                      css={{
                        color: theme.darkGrey,
                        background: '#eee',
                        width: '65%',
                      }}
                      className="pl-3 my-2 mb-3 text-lg font-normal">
                      {key}
                    </h2>
                  )}
                  {compose(renderSnippets(), pathOr([], [key]))(snippetList)}
                </div>
              )),
              keys
            )(snippetList)}
          </SnippetPreviewWrapper>
        </Col>
      </Row>
      <Divider />
      <Row gutter={16}>
        <Col span={8}>
          <Form.Item {...formItemLayout} label="判讀醫師">
            <Input bordered={false} />
          </Form.Item>
        </Col>
        <Col span={8}>
          <Form.Item {...formItemLayout} label="複審醫師">
            <Input bordered={false} />
          </Form.Item>
        </Col>
        <Col span={8}>
          <Form.Item {...formItemLayout} label="申請醫師">
            <Input bordered={false} />
          </Form.Item>
        </Col>
      </Row>
      <Row gutter={16}>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="報告輸入人員">
            <Input bordered={false} />
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="報告複審人員">
            <Input bordered={false} />
          </Form.Item>
        </Col>
      </Row>
      <Divider />
      <Row className="mb-4">
        <Col span={24}>
          <div className="text-center">病解專醫字第000001號</div>
        </Col>
      </Row>
    </div>
  )
}
