import React from 'react'
import { RouteComponentProps } from 'react-router-dom'
import { Row, Col, BackTop, Affix, Form, Select, message } from 'antd'
import { BulbOutlined } from '@ant-design/icons'
import cx from 'classnames'
import {
  map,
  pathOr,
  keys,
  gt,
  __,
  length,
  ifElse,
  equals,
  always,
  filter,
  values,
  not,
  replace,
  split,
  join,
} from 'ramda'
import useTitle from '@/hooks/useTitle'
import useVisible from '@/hooks/useVisible'
import { useBreadcrumb, useHeaderContent } from '@/hooks/useApp'
import {
  useToggleReportPreview,
  snippetCategoryOfReport,
} from '@/data/useReportTemplate'
import { isNil, queryString, uuid } from '@/utils/webHelper'
import { ReportTemplate, SnippetProps } from '@/constants/types'
import { Page } from '@/components/layout'
import ReactDndProvider from '@/components/snippet-scrollable-container'
import SnippetsContainer from '@/components/snippets-container'
import Input from '@/components/input'
import Collapse from '@/components/collapse'
import SnippetListMenu from '@/components/snippet-list-menu'
import { renderSnippets } from '@/components/snippet-utils'
import Tabs from '@/components/draggable-tabs'
import {
  useAllReportTemplates,
  useReportTemplate,
} from '@/services/reportTemplate'
import {
  useReportItem,
  useAddNewReport,
  useUpdateReport,
} from '@/services/report'
import { usePathology } from '@/services/pathology'
import history from '@/utils/history'
import { REPORT_EDIT } from '@/constants/routes'
import Header from './header'
import Pie from './pie'
import ReportMedicalOrder from './report-typing-medical-order'
import ReportAffix from './report-typing-affix'
import ReportPreview from './report-typing-preview'
import PhraseListModal from './phrase-list-modal'
const { compose } = require('ramda')

enum menuState {
  VISIBLE_RIGHT = '1',
  VISIBLE_LEFT = '2',
}

interface Props extends RouteComponentProps<{ id: string }> {
  id: any
  reviewDoctor: string
  serialNo: string
}

const col = ifElse(equals(false), always(5), always(0))

export default function ReportTyping(props: Props) {
  useTitle('輸入報告')

  useBreadcrumb([{ name: '輸入報告' }, { name: '病理報告輸入' }])

  useToggleReportPreview(true)

  const [form] = Form.useForm()

  const [reportTemplates] = useAllReportTemplates()

  const [keyword, setKeyword] = React.useState<string>()

  const [exportable, handleExportable] = useVisible()

  const [phraseListModalVisible, handlePhraseListModalVisible] = useVisible()

  const { data /* isValidating */ } = usePathology()

  const [visibleLayouts, setVisibleLayouts] = React.useState<string[]>([
    menuState.VISIBLE_LEFT,
    menuState.VISIBLE_RIGHT,
  ])

  // 沒有 QS 代表是編輯，會有 params.id 要用 useReportItem 取得報告資料後，
  // 再餵進去 useReportTemplate 作編輯
  const { params } = props.match

  // 有 QS 代表新增，會沒有 params.id，此時的資料要用 reportTemplate
  const qs = queryString(props.location.search)

  // 只有新增時有 QS 才會有
  const [templateId, setCurrentTemplateId] = React.useState(qs.templateId)

  React.useEffect(() => {
    setCurrentTemplateId(qs.templateId)
  }, [qs.templateId])

  const item = useReportItem(params.id)

  const {
    total,
    category,
    reportTemplate,
    snippetList,
    handleTabEdit,
    handleTabChange,
    handleTabMove,
    handleTabNameChange,
    handleSnippetUpdate,
  } = useReportTemplate({
    id: templateId,
    data: item,
  })

  React.useEffect(() => {
    const handleKeyPress = (evt: KeyboardEvent) => {
      // ctrl + s
      if (evt.ctrlKey && evt.keyCode === 83) {
        handleOk()
      }
      // f9
      if (!evt.ctrlKey && evt.keyCode === 120) {
        handlePhraseListModalVisible()
      }
    }

    window.addEventListener('keydown', handleKeyPress)

    return () => {
      window.removeEventListener('keydown', handleKeyPress)
    }
  }, []) // eslint-disable-line

  React.useEffect(() => {
    form.setFieldsValue(reportTemplate)
  }, [JSON.stringify(reportTemplate)]) // eslint-disable-line

  React.useEffect(() => {
    if (qs.reviewDoctor) {
      form.setFieldsValue({ reviewDoctor: qs.reviewDoctor })
    }
    if (qs.serialNo) {
      form.setFieldsValue({ serialNo: qs.serialNo })
    }
  }, [JSON.stringify(qs)]) // eslint-disable-line

  const handleLayoutChange = (value: string) => {
    setVisibleLayouts((prev: string[]) =>
      prev.includes(value) ? prev.filter(x => x !== value) : [...prev, value]
    )
  }

  const leftContentVisible = visibleLayouts.includes(menuState.VISIBLE_LEFT)
  const rightContentVisible = visibleLayouts.includes(menuState.VISIBLE_RIGHT)

  const [, handleAdd] = useAddNewReport({
    onCompleted: ({ id }: any) => {
      history.push(replace(':id', id)(REPORT_EDIT))
      message.success('報告資料已新增')
    },
  })

  const [, handleUpdate] = useUpdateReport({
    onCompleted: () => message.success('報告資料已儲存'),
  })

  const handleOk = () => {
    form.validateFields().then(values => {
      let payload = {
        ...values,
        ...(params.id ? { id: params.id } : {}),
        ...data,
        setting: snippetList,
      }

      const handler = params.id ? handleUpdate : handleAdd

      handler(payload)
    })
  }

  useHeaderContent(
    <Header
      leftContentVisible={leftContentVisible}
      rightContentVisible={rightContentVisible}
      onLayoutChange={handleLayoutChange}
      isExport={exportable}
      onToggleExport={handleExportable}
      onOk={handleOk}
    />
  )

  const handleReportSearch = (value: string) => {
    setKeyword(value)
  }

  const handleReportChange = (value: string) => {
    setCurrentTemplateId(value)
  }

  const [affixed, setIsAffixed] = React.useState<boolean | undefined>(false)

  const [percentage, setPercentage] = React.useState(0)

  const handleAffixChange = (affixed?: boolean) => {
    setIsAffixed(affixed)
  }

  const handleAnchorClick = (
    e: React.MouseEvent<HTMLElement>,
    link: {
      title: React.ReactNode
      href: string
    }
  ) => {
    e.preventDefault()

    const [tab, href] = compose(
      ([tab, ...rest]: string[]) => [tab, join('-')(rest)],
      split('-'),
      pathOr('', ['href'])
    )(link)

    if (category !== 'overview') {
      handleTabChange(tab)
    }

    if (!href) {
      return
    }

    window.scroll({
      behavior: 'smooth',
      left: 0,
      top: document.getElementById(href)?.offsetTop,
    })
  }

  const handlePhraseAdd = (values: SnippetProps[]) => {
    let newList = [
      ...snippetList[category],
      ...values.map(x => ({
        ...x,
        fromMenu: false,
        id: `${x.id}__${uuid()}`,
      })),
    ]

    handleSnippetUpdate(newList)
  }

  const handleValuesChange = React.useCallback(
    (_: any, { data }: { data: { [key: string]: any } }) => {
      setPercentage(
        Math.round(
          (compose(length, filter(compose(not, isNil)), values)(data) / total) *
            100
        )
      )
    },
    [total]
  )

  React.useEffect(() => {
    handleValuesChange(null, form.getFieldsValue())
  }, [total]) // eslint-disable-line

  const renderCategory = (category: string) => {
    return compose(gt(__, 0), length, pathOr([], [category]))(snippetList) ? (
      <h2 id={category} className="my-2">
        {category}
      </h2>
    ) : null
  }

  const colSpan = 14 + col(leftContentVisible) + col(rightContentVisible)

  const filterReportTemplates: ReportTemplate[] = React.useMemo(
    () =>
      ifElse(isNil, always(reportTemplates), val =>
        filter((x: ReportTemplate) => x.name.includes(val))(reportTemplates)
      )(keyword),
    [keyword] // eslint-disable-line
  )

  const { reviewDoctor } = props

  return (
    <ReactDndProvider className="is--preview">
      {phraseListModalVisible && (
        <PhraseListModal
          onOk={handlePhraseAdd}
          onCancel={handlePhraseListModalVisible}
        />
      )}
      <BackTop />
      <Page>
        <Form
          form={form}
          labelAlign={exportable ? 'left' : 'right'}
          onValuesChange={handleValuesChange}>
          <Row gutter={16}>
            {leftContentVisible && (
              <Col span={5}>
                <Collapse
                  defaultActiveKey={[1, 2]}
                  expandIcon={() => null}
                  className="shadow-2xl">
                  <Collapse.Panel
                    header={<div className="text-center">報告內容</div>}
                    key={1}>
                    <Row className="mb-2 text-sm text-darkGrey">
                      <Col span={24} className="mb-3">
                        <div className="inline-block" css={{ width: 65 }}>
                          判讀醫師
                        </div>
                        <Form.Item noStyle name="reviewDoctor">
                          <Input className="w-2/3" bordered={false} />
                        </Form.Item>
                      </Col>
                      <Col span={24} className="my-3">
                        <div className="inline-block" css={{ width: 65 }}>
                          申請序號
                        </div>
                        <Form.Item noStyle name="serialNo">
                          <Input className="w-2/3" bordered={false} />
                        </Form.Item>
                      </Col>
                      <Col span={24} className="my-3">
                        <div className="inline-block" css={{ width: 65 }}>
                          報告格式
                        </div>
                        <Select
                          showSearch
                          className="w-2/3"
                          bordered={false}
                          filterOption={false}
                          value={templateId}
                          onSearch={handleReportSearch}
                          onChange={handleReportChange}>
                          {filterReportTemplates.map(x => (
                            <Select.Option key={x.id} value={x.id}>
                              {x.name}
                            </Select.Option>
                          ))}
                        </Select>
                      </Col>
                      <Col span={24} className="mt-3">
                        <ReportAffix
                          affixed={affixed}
                          onClick={handleAnchorClick}
                          onChange={handleAffixChange}
                          snippetList={snippetList}
                        />
                      </Col>
                    </Row>
                  </Collapse.Panel>
                  <Collapse.Panel
                    header={<div className="text-center">模版工具</div>}
                    key={2}>
                    <Row>
                      <Col span={24}>
                        <SnippetListMenu />
                        <div
                          className="flex items-center mt-2 cursor-pointer"
                          onClick={handlePhraseListModalVisible}>
                          <BulbOutlined className="mr-2" />
                          Phrase
                        </div>
                      </Col>
                    </Row>
                  </Collapse.Panel>
                </Collapse>
              </Col>
            )}

            <Col span={colSpan}>
              {!exportable && (
                <>
                  <div className="pt-2 bg-white rounded-lg shadow-2xl">
                    <Affix offsetTop={0}>
                      <div
                        className={cx(
                          'px-4 mb-4 -mt-2',
                          'flex items-center justify-between',
                          'rounded-lg shadow-sm',
                          'text-xl text-darkGrey font-medium'
                        )}
                        css={{ background: '#fff' }}>
                        <div>
                          Pathological Image Report
                          <div className="text-base text-gray-600">
                            Imaging Report From for Hepatocellular Carcinoma
                          </div>
                        </div>
                        <Pie
                          percent={percentage}
                          total={`${percentage}%`}
                          style={{ width: 70, paddingTop: 6 }}
                          height={80}
                        />
                      </div>
                    </Affix>
                  </div>
                  <div className="rounded-lg shadow-2xl">
                    <Tabs
                      type="editable-card"
                      activeKey={category}
                      onChange={handleTabChange}
                      onEdit={handleTabEdit}
                      tabBarGutter={8}
                      moveTabNode={handleTabMove}
                      className="flex-1">
                      {compose(
                        map((category: string) => (
                          <Tabs.TabPane
                            closable
                            key={category}
                            tab={
                              <span
                                contentEditable
                                suppressContentEditableWarning
                                spellCheck={false}
                                className="outline-none"
                                css={{ whiteSpace: 'nowrap' }}
                                onChange={handleTabNameChange(category)}>
                                {category}
                              </span>
                            }>
                            <Col
                              span={24}
                              className="p-4 report-template-builder__main-content">
                              {category ===
                                snippetCategoryOfReport.PATHOLOGICAL_DIAGNOSIS && (
                                <div
                                  className="mb-4 px-1"
                                  dangerouslySetInnerHTML={{
                                    __html: data?.ORWORD,
                                  }}
                                />
                              )}
                              <SnippetsContainer
                                // draggable={!isPreview}
                                draggable
                                snippetList={snippetList[category]}
                                onSnippetUpdate={handleSnippetUpdate}
                              />
                            </Col>
                          </Tabs.TabPane>
                        )),
                        keys
                      )(snippetList)}
                      <Tabs.TabPane key="overview" tab="Overview">
                        {compose(
                          map((category: string) => (
                            <div key={category} className="px-3 bg-white">
                              {renderCategory(category)}
                              {compose(
                                renderSnippets({ className: 'mb-0' }),
                                pathOr([], [category])
                              )(snippetList)}
                            </div>
                          )),
                          keys
                        )(snippetList)}
                      </Tabs.TabPane>
                    </Tabs>
                  </div>
                </>
              )}
              {exportable && (
                <div className="rounded-log shadow-2x bg-white p-3 px-5">
                  <div id="editor" />
                  <ReportPreview
                    data={{
                      template: reportTemplate,
                      medical: data,
                      snippetList,
                    }}
                  />
                </div>
              )}
            </Col>

            {rightContentVisible && (
              <Col span={5}>
                <ReportMedicalOrder data={data} reviewDoctor={reviewDoctor} />
              </Col>
            )}
          </Row>
        </Form>
      </Page>
    </ReactDndProvider>
  )
}
