import useSwr from 'swr'
import request from '@/utils/request'
import { pathOr, head } from 'ramda'
const { compose } = require('ramda')

export type MedicalOrder = {
  CFINANCL: string
  CSECT: string
  HBED: string
  HBIRTHDT: string
  HHISNUM: string
  HNAMEC: string
  HNURSTA: string
  HSEX: string
  ORDATE: string
  ORDCLNM: string
  ORDOCNM: string
  ORHPCODE: string
  ORPROCED: string
  ORREQNO: string
  ORSPENAM: string
  ORSTATUS: string
  ORWORD: string
  ORWORKNO: string
  ZRSREGORD: string
}

export function usePathology() {
  const { data, ...others } = useSwr(`/api/b2/getPathologyResult`, url =>
    request(url, {
      method: 'POST',
      body: {
        classification: 'pathorderview',
        noType: 'HHISNUM',
        no: '16685033',
      },
    })
  )

  return {
    ...others,
    data: compose(head, pathOr([], []))(data) as MedicalOrder,
  }
}

