import store from 'store'
import moment from 'moment'
import { update, length } from 'ramda'
import { Mutation, MutationOptions, PhraseTemplate } from '@/constants/types'
import { usePhraseTemplates, usePhraseTemplate } from '@/data/usePhrase'
import { uuid } from '@/utils/webHelper'

export const usePhraseList = () => {
  const [phraseTemplates] = usePhraseTemplates()

  const refetch = () => ({})

  return {
    refetch,
    data: {
      content: phraseTemplates,
      total: length(phraseTemplates),
      totalPages: Math.floor(length(phraseTemplates) / 10 + 1),
    },
    loading: false,
  }
}

export const useAddNewPhrase = ({ onCompleted }: MutationOptions = {}) => {
  const [phraseTemplates, setPhraseTemplates] = usePhraseTemplates()

  const handler = (values: PhraseTemplate) => {
    let id = uuid()
    let newList = [
      ...phraseTemplates,
      {
        ...values,
        id,
        createdAt: moment(),
        createdAtStr: moment().format('YYYY/MM/DD HH:mm'),
        updatedAt: moment(),
        updatedAtStr: moment().format('YYYY/MM/DD HH:mm'),
      },
    ]

    setPhraseTemplates(newList)

    store.set('phraseTemplateListState', newList)

    if (onCompleted) {
      onCompleted({ ...values, id })
    }
  }

  return [false, handler] as Mutation
}

export const useUpdatePhrase = ({ onCompleted }: MutationOptions = {}) => {
  const [phraseTemplates, setPhraseTemplates] = usePhraseTemplates()

  const handler = (values: PhraseTemplate) => {
    let index = phraseTemplates.findIndex(x => `${x.id}` === `${values.id}`)
    let newList = update(
      index,
      {
        ...values,
        updatedAt: moment(),
        updatedAtStr: moment().format('YYYY/MM/DD HH:mm'),
      },
      phraseTemplates
    )

    setPhraseTemplates(newList)
    store.set('phraseTemplateListState', newList)

    if (onCompleted) {
      onCompleted()
    }
  }

  return [false, handler] as Mutation
}

export const useRemovePhrase = ({ onCompleted }: MutationOptions = {}) => {
  const [phraseTemplates, setPhraseTemplates] = usePhraseTemplates()

  const handler = (values: PhraseTemplate) => {
    let newList = phraseTemplates.filter(x => `${x.id}` !== `${values.id}`)

    setPhraseTemplates(newList)
    store.set('phraseTemplateListState', newList)

    if (onCompleted) {
      onCompleted()
    }
  }

  return [false, handler] as Mutation
}

export const usePhraseItem = (options: { id?: React.Key; data?: any }) => {
  const { setSnippetList, snippetList, ...rest } = usePhraseTemplate(options)

  return {
    ...rest,
    snippetList,
    setSnippetList,
    handleSnippetUpdate: setSnippetList,
  }
}
