import React from 'react'
import { CheckOutlined } from '@ant-design/icons'
import moment from 'moment'
import { replace, join, dropLast, split } from 'ramda'
import useRouteLeaveConfirm from '@/hooks/useRouteLeaveConfirm'
import { envStore } from '@/constants/env'
import Button from '@/components/button'
import Select from '@/components/select'
import { REPORT_INQUIRY, RADIOL_IMAGE } from '@/constants/routes'
const { compose } = require('ramda')
const html2pdf = require('html2pdf.js')

export enum menuState {
  VISIBLE_RIGHT = '1',
  VISIBLE_LEFT = '2',
}

type Props = {
  rightContentVisible: boolean
  leftContentVisible: boolean
  isExport?: boolean
  onLayoutChange: (value: string) => void
  onToggleExport: () => void
  onOk: () => void
  onExportJson?: () => void
  onExport?: () => void
}

export default function Header({
  leftContentVisible,
  rightContentVisible,
  isExport,
  onLayoutChange,
  onToggleExport,
  onOk,
}: Props) {
  const handleConfirm = useRouteLeaveConfirm(REPORT_INQUIRY)

  const [exporting, setIsExporting] = React.useState(false)

  const handleSave = () => {
    onOk()
  }

  const handleOpenImageViewer = () => {
    let pathname = replace(
      ':studyInstanceUIDs',
      '1.3.6.1.4.1.14519.5.2.1.3098.4963.119176436435873112231353675460'
    )(RADIOL_IMAGE)

    // remove last `/`
    let url = compose(join('/'), dropLast(1), split('/'))(envStore.appUrl)

    window.open(`${url}${pathname}`, '__blank')
  }

  const handleExport = async () => {
    setIsExporting(true)

    const element = document.querySelector('.js-export-pdf')
    const opt = {
      margin: 1,
      filename: `報告匯出_${moment().format('YYYYMMDDHHmmss')}.pdf`,
      image: { type: 'jpeg', quality: 0.98 },
      html2canvas: { scale: 2 },
      jsPDF: { unit: 'in', format: 'letter', orientation: 'portrait' },
    }

    // New Promise-based usage:
    html2pdf().set(opt).from(element).save()
  }

  return (
    <div className="inline-flex">
      {!isExport && (
        <>
          <Select
            mode="multiple"
            placeholder="檢 視"
            className="is--outline cursor-pointer"
            style={{ minWidth: 80 }}
            dropdownMatchSelectWidth={false}
            dropdownStyle={{ minWidth: 138 }}
            autoFocus={false}
            onSelect={onLayoutChange}
            value={[]}>
            <Select.Option value={menuState.VISIBLE_LEFT}>
              <div className="flex items-center">
                顯示左邊內容
                {leftContentVisible && <CheckOutlined className="ml-4" />}
              </div>
            </Select.Option>
            <Select.Option value={menuState.VISIBLE_RIGHT}>
              <div className="flex items-center">
                顯示右邊內容
                {rightContentVisible && <CheckOutlined className="ml-4" />}
              </div>
            </Select.Option>
          </Select>
          <Button
            color="outline"
            className="ml-2 text-white border-white bg-transparent"
            onClick={handleOpenImageViewer}
            style={{ minWidth: 80 }}>
            影像
          </Button>
          <Button
            color="outline"
            className="ml-2 text-white border-white bg-transparent"
            onClick={handleSave}
            style={{ minWidth: 80 }}>
            儲存
          </Button>
          <Button
            color="outline"
            className="ml-2 text-white border-white bg-transparent"
            onClick={handleConfirm}
            style={{ minWidth: 80 }}>
            取消
          </Button>
          <Button
            color="outline"
            className="ml-6 text-white border-white bg-transparent"
            onClick={onToggleExport}>
            輸出報告
          </Button>
        </>
      )}
      {isExport && (
        <>
          <Button
            color="outline"
            className="text-white border-white bg-transparent"
            onClick={onToggleExport}>
            取消
          </Button>
          <Button
            color="outline"
            className="ml-6 text-white border-white bg-transparent">
            下載 JSON
          </Button>
          <Button
            color="outline"
            className="ml-2 text-white border-white bg-transparent"
            loading={exporting}
            onClick={handleExport}>
            下載報告
          </Button>
        </>
      )}
    </div>
  )
}
