import { atom, useRecoilState } from 'recoil'
import Store from 'store'
import { setComponentByTab } from '@/components/snippet-template-list'

export const reportListState = atom({
  key: 'reportListState',
  default: setComponentByTab(Store.get('reportListState') || []),
})

// 輸入報告清單
export function useReportList() {
  return useRecoilState(reportListState)
}
